@import url('https://fonts.googleapis.com/css2?family=Bubblegum+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Aladin&display=swap');

.bubblegum-font {
    font-family: Bubblegum Sans;
    font-weight: 900; }
.masthead {
    background-image: url("../../../images/artistic-sunset.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
 }    // animation: backgroundChanging 500s alternate infinite

@keyframes backgroundChanging {
    0% {
        background-image: url("../../../images/artistic-sunset.jpg"); }
    10% {
        background-image: url("../../../images/artistic-sunset-1.jpg"); }
    20% {
        background-image: url("../../../images/artistic-sunset-2.jpg"); }
    30% {
        background-image: url("../../../images/artistic-sunset-3.jpg"); }
    40% {
        background-image: url("../../../images/artistic-sunset-4.jpg"); }
    50% {
        background-image: url("../../../images/artistic-sunset-5.jpg"); }
    60% {
        background-image: url("../../../images/artistic-sunset-6.jpg"); }
    70% {
        background-image: url("../../../images/artistic-sunset-7.jpg"); }
    80% {
        background-image: url("../../../images/artistic-sunset-8.jpg"); }
    90% {
        background-image: url("../../../images/artistic-sunset-9.jpg"); }
    100% {
        background-image: url("../../../images/artistic-sunset-10.jpg"); } }

.brand-heading {
    font-family: Aladin, cursive;
    font-size: 450%;
    font-weight: 900;
    color: white; }
.intro-text {
    color: white;
    font-size: 150%; }
.intro-container {
    background: #2e0e0e41;
    box-shadow: 6px 4px 20px 2px #aca8a841;
    padding: 50px 0px; }
@keyframes jumpingText {
    0% {
        position: relative;
        top: -240px;
        color: orange;
        transform: rotate(0deg);
        text-shadow: 1px 1px 40px red;
        visibility: hidden;
        opacity: 0.2; }
    100% {
        transform: rotate(20deg);
        position: relative;
        top: 10px;
        color: white;
        text-shadow: 0px 0px 0px 0px #fff;
        visibility: visible;
        opacity: 1; } }
@keyframes bookNow {
    0% {
        border-radius: 10px 2px 4px 20px; }
    20% {
        border-radius: 5px 8px 6px 5px; }
    40% {
        border-radius: 12px 16px 2px 10px; }
    60% {
        border-radius: 20px 10px 20px 2px; }
    80% {
        border-radius: 5px 8px 6px 5px; }
    100% {
        border-radius: 20px 6px 8px 10px; } }

