/* Base */

*{
    scroll-behavior: smooth !important;
}
body {
    line-height: 1.7;
    color: gray;
    font-weight: 800 !important;
    font-size: 1rem;
    font-family: Arsenal !important;
}


.blank-top{
    background: black;
    min-height: 110px;
    width: 100%; 
    display: block;
}
.marquee {
    width: 90vw;
    line-height: 50px;
    white-space: nowrap;
    overflow: hidden;
    box-sizing: border-box;
}
.marquee p {
    display: inline-block;
    padding-left: 100%;
    animation: marquee 15s linear infinite;
}
@keyframes marquee {
    0%   { transform: translate(0, 0); }
    10%   { transform: translate(-30%, 0); }
    90%   { transform: translate(-70%, 0); }
    100% { transform: translate(-100%, 0); }
}

::selection {
    background: #000;
    color: #fff;
}

a {
    transition: .3s all ease;
}

a:hover {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #000;
}

.text-black {
    color: #000 !important;
}

.bg-black {
    background: #000 !important;
}

.site-wrap:before {
    transition: .3s all ease-in-out;
    background: rgba(0, 0, 0, 0.6);
    content: "";
    position: absolute;
    z-index: 2000;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    visibility: hidden;
}

.text-orange{
    color: orange !important;
}
.text-orangered{
    color: orangered !important;
}

.offcanvas-menu .site-wrap {
    height: 100%;
    width: 100%;
    z-index: 2;
    overflow: hidden;
}

.offcanvas-menu .site-wrap:before {
    opacity: 1;
    visibility: visible;
}

.offcanvas-menu {
    position: relative;
}

.offcanvas-menu:after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    top: 0;
    right: 0;
    z-index: 21;
    background: rgba(0, 0, 0, 0.2);
}

.btn {
    border-width: 2px;
}

.btn:active,
.btn:focus {
    outline: none;
    box-shadow: none !important;
}

.btn.btn-lg {
    padding: 10px 20px;
    font-size: 16px;
}

.btn.btn-primary {
    color: #fff;
}

.btn.btn-primary:hover,
.btn.btn-primary:focus,
.btn.btn-primary:active {
    color: #000;
    background-color: transparent !important;
}

.bg-black {
    background: #000;
}

.form-control {
    height: 47px;
}

.form-control:active,
.form-control:focus {
    border-color: #ff5733;
}

.form-control:hover,
.form-control:active,
.form-control:focus {
    box-shadow: none !important;
}

.site-section {
    padding: 2.5em 0;
}

@media (min-width: 768px) {
    .site-section {
        padding: 5em 0;
    }
}

.site-section.site-section-sm {
    padding: 4em 0;
}

.site-section-heading {
    font-size: 30px;
    color: #dee2e6;
    position: relative;
}

.site-section-heading:before {
    content: "";
    left: 0%;
    top: 0;
    position: absolute;
    width: 40px;
    height: 2px;
    background: #ff5733;
}

.site-section-heading.text-center:before {
    content: "";
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    position: absolute;
    width: 40px;
    height: 2px;
    background: #ff5733;
}

.border-top {
    border-top: 1px solid #ced4da !important;
}

.site-footer {
    padding: 4em 0;
    background: #333333;
}

.site-footer p {
    color: #737373;
}

.site-footer h2,
.site-footer h3,
.site-footer h4,
.site-footer h5 {
    color: #fff;
}

.site-footer a {
    color: #999999;
}

.site-footer a:hover {
    color: white;
}

.site-footer ul li {
    margin-bottom: 10px;
}

.site-footer .footer-heading {
    font-size: 20px;
    color: #fff;
}

.bg-text-line {
    display: inline;
    background: #000;
    box-shadow: 20px 0 0 #000, -20px 0 0 #000;
}

.bg-image {
    background-size: cover;
    background-repeat: no-rpeeat;
    overflow: hidden;
}

.bg-image.center {
    background-position: top center;
}

.bg-image.fixed {
    background-position: fixed !important;
}

.bg-image.overlay,
