.person figure {
    position: relative;
    margin-bottom: 0;
}

.person figure:before {
    display: none;
    position: absolute;
    content: "";
    z-index: 1;
    left: 10px;
    top: 10px;
    right: 10px;
    bottom: 10px;
    border: 2px solid #ff5733;
    opacity: 1;
    visibility: visible;
    transition: .3s all ease;
}

.person h2 {
    font-size: 18px;
}

.person .bio-img {
    position: relative;
    text-align: center;
    overflow: hidden;
    margin-bottom: 30px;
}

.person .bio-img img {
    position: relative;
    transition: .3s all ease-in-out;
    transform: scale(1);
}

.person .bio-img .social a {
    display: inline-block;
    padding: 5px;
    color: #fff;
}

.person:hover img {
    transform: scale(1.05);
}

.person:hover figure:before {
    opacity: 0;
    visibility: hidden;
}

.post-entry,
.project-entry {
    overflow: hidden;
    position: relative;
    display: block;
}

.post-entry .figure,
.project-entry .figure {
    overflow: hidden;
    margin-bottom: 20px;
}

.post-entry h3,
.project-entry h3 {
    font-size: 18px;
    line-height: 1.3;
}

.post-entry h3 a,
.project-entry h3 a {
    color: #000;
}

.post-entry img,
.project-entry img {
    transition: .3s all ease;
    transform: scale(1);
}

.post-entry:hover img,
.project-entry:hover img {
    transform: scale(1.05);
}

.testimony img {
    max-width: 100px;
    border-radius: 50%;
    margin: 0 auto 30px auto;
}

.testimony .sub-title {
    margin-bottom: 30px;
}

.testimony h3 {
    font-size: 18px;
}

.testimony p {
    font-size: 20px;
}

.site-block-testimony img {
    max-width: 140px;
    border-radius: 50%;
}

.site-block-testimony p {
    font-size: 1.2rem;
}

.block-12 figure {
    position: relative;
}

.block-12 figure:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(to bottom, transparent 0%, transparent 18%, rgba(0, 0, 0, 0.8) 99%, rgba(0, 0, 0, 0.8) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#cc000000', GradientType=0);
}

.block-12 .text-overlay h2 {
    color: #fff;
}

.block-16 figure {
    position: relative;
}

.block-16 figure .play-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
    width: 70px;
    height: 70px;
    display: block;
    border-radius: 50%;
    opacity: 1;
    color: #ff5733 !important;
}

.block-16 figure .play-button:hover {
    opacity: 1;
}

.block-16 figure .play-button > span {
    position: absolute;
    left: 55%;
    top: 50%;
    transform: translate(-60%, -50%);
}

.block-25 ul,
.block-25 ul li {
    padding: 0;
    margin: 0;
}

.block-25 ul li .date {
    color: rgba(255, 255, 255, 0.25);
    font-size: 0.8rem;
    display: block;
}

.block-25 ul li a .image {
    flex: 0 0 90px;
}

.block-25 ul li a .image img {
    border-radius: 4px;
    max-width: 100%;
    opacity: 1;
    transition: .3s all ease-in-out;
}

.block-25 ul li a .text .heading {
    font-size: 18px;
    line-height: 1.5;
    margin: 0;
    padding: 0;
    transition: .3s all ease;
    color: #999999;
}

.block-25 ul li a .meta {
    color: #ff5733;
}

.block-25 ul li a:hover img {
    opacity: .5;
}

.block-25 ul li a:hover .text .heading {
    color: #fff;
}

.pricing .price {
    color: #000;
}

.pricing .price .currency {
    position: relative;
    top: -1em;
    font-size: 20px;
}

.pricing .price .number {
    font-size: 40px;
    color: #000;
    font-weight: bold;
}

.pricing li.text-muted:before {
    color: #dee2e6;
}

.play-button {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    position: relative;
    margin: 0 auto 30px auto;
    transform: scale(1);
    transition: .3s all ease-in-out;
}

.play-button > span {
    position: absolute;
    top: 50%;
    color: #000;
    left: 50%;
    transform: translate(-40%, -50%);
}

.play-button:hover {
    transform: scale(1.2);
}

/*PRELOADING------------ */
#overlayer {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 7100;
    /* background: #fff; */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.loader {
    z-index: 7700;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.form-subscribe .btn:hover,
.form-subscribe .btn:active,
.form-subscribe .btn:focus {
    background-color: #ff5733 !important;
    border-color: #ff5733 !important;
    color: #fff;
}

